/*-----------------------------------------------------------------------------------------*/
/* SIN SOPORTE GRID */
/*-----------------------------------------------------------------------------------------*/

.uoc-container {
    display: block;
    margin: 0px;
    padding: 1em;
    width: 100%;
    height: 100%;
    background-image: url("../images/fotoMusicaCC.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-color: blue;
}


.caja1 {
    background-color: yellow;
    margin-left: 5em;
    width: 20%;
    clear: both;

}

.caja2 {
    background-color: red;
    margin-top: 2em;
    margin-left: 10%;
    width: 20%;
    float: left;
}

.caja3 {
    background-color: blue;
    margin-top: 2em;
    margin-left: 10%;
    width: 20%;
    float: left;
}

.caja4 {
    background-color: purple;
    margin-top: 2em;
    margin-left: 10%;
    width: 20%;
    float: left;

}

.caja5 {
    background-color: pink;
    margin-top: 2em;
    margin-left: 20%;
    width: 25%;
    float: left;
    clear: left;
}

.caja6 {
    background-color: black;
    margin-top: 2em;
    margin-left: 10%;
    width: 25%;
    float: left;
}

.caja7 {
    height: auto;
    background-color: lightcyan;
    margin-top: 2em;
    margin-left: 5%;
    width: 85%;
    float: left;
}

@media (max-width:576px) {
    .uoc-container {
        height: auto;
        overflow: auto;
        background-color: red;
    }

    .caja1,
    .caja2,
    .caja3,
    .caja4,
    .caja5,
    .caja6,
    .caja7 {
        float: none;
        width: 96%;
        padding: 0px;
        margin-left: 2%;
        margin-right: 2%;
    }


}